<template>
	<div class="mangban">
		<van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="pageBack" @click-right="openAction" />
		<div class="header-cell">
			<!--  -->
			<cell-value title="" required :value="sendData.applyDept" class="readonly"></cell-value>
			<!--  -->
			<cell-value title="" required :value="sendData.applyer" class="readonly"></cell-value>
			<!--  -->
			<cell-value title="" value class="readonly"></cell-value>
			<!--  -->
			<cell-value title="" value="" class="readonly"></cell-value>
		</div>
		<div class="cell_group" v-for="(item, index) in sendData.pipe" :key="index">
			<!--  -->
			<cell-value :title="'' + (index + 1)" class="guandao-id"></cell-value>
			<!--  -->
			<div class="cell">
				<div class="cell_title">
					<span></span>
				</div>
				<div class="cell_input">
					<input type="text" placeholder="" v-model="item.pipeName" />
				</div>
			</div>
			<!--  -->
			<div class="cell">
				<div class="cell_title">
					<span></span>
				</div>
				<div class="cell_input">
					<input type="text" placeholder="" v-model="item.pipeMedium" />
				</div>
			</div>
			<!--  -->
			<div class="cell">
				<div class="cell_title">
					<span></span>
				</div>
				<div class="cell_input">
					<input type="text" placeholder="" v-model="item.pipeTemp" />
				</div>
			</div>
			<!--  -->
			<div class="cell">
				<div class="cell_title">
					<span></span>
				</div>
				<div class="cell_input">
					<input type="text" placeholder="" v-model="item.pipePressure" />
				</div>
			</div>
			<!--  -->
			<cell-picker title="" v-model="sendData.pipe[index].pipeMaterial" :columns="materialColumns" />
			<!--  -->
			<div class="cell">
				<div class="cell_title">
					<span></span>
				</div>
				<div class="cell_input">
					<input type="text" placeholder="" v-model="item.pipeSpec" />
				</div>
			</div>
			<!--  -->
			<div class="cell">
				<div class="cell_title">
					<span></span>
				</div>
				<div class="cell_input">
					<input type="text" placeholder="" v-model="item.pipeNumber" />
				</div>
			</div>
			<!--  -->
			<cell-time v-model="item.pipePullTime" title="" />
			<!--  -->
			<cell-time v-model="item.pipeBlockTime" title="" />
			<!--  -->
			<cell-select-user title="" :storeModule="storeModule" :storeKey="'pipeBlockOperator'+ index" v-model="sendData.pipe[index].pipeBlockOperator" />
			<!--  -->
			<cell-select-user title="" :storeModule="storeModule" :storeKey="'pipePullOperator'+ index" v-model="sendData.pipe[index].pipePullOperator" />
			<!--  -->
			<cell-select-user title="" :storeModule="storeModule" :storeKey="'pipeBlockGuardian'+ index" v-model="sendData.pipe[index].pipeBlockGuardian" />
			<!--  -->
			<cell-select-user title="" :storeModule="storeModule" :storeKey="'pipePullGuardian'+ index" v-model="sendData.pipe[index].pipePullGuardian" />

			<!--  -->
			<div class="add_device" v-if="index == sendData.pipe.length - 1">
				<div class="add_device_action" @click="addDevice">
					<van-icon name="add-o" color="#6096F8" />
				</div>
			</div>
		</div>
		<!--  -->
		<div class="cell_group">
			<!--  -->
			<cell-select-user title="" required :storeModule="storeModule" storeKey="scMan" v-model="sendData.scMan"></cell-select-user>
			<!--  -->
			<cell-select-user title="" required :storeModule="storeModule" storeKey="zyMan" v-model="sendData.zyMan"></cell-select-user>
			<!--  -->
			<cell-select-tag required title="" storeKey="otherSpecial" :tagList="otherSpecial" :showList="list_1"
			 :storeModule="storeModule"></cell-select-tag>
		</div>
		<!--  -->
		<van-popup v-model="timeShow" position="bottom">
			<van-datetime-picker v-model="currentDate" type="datetime" :min-date="new Date()" @cancel="onTimeCancel" @confirm="onTimeConfirm" />
		</van-popup>

		<!--  -->
		<div class="confirm">
			<div class="head">
				<div class="head_1"></div>
				<div class="head_2"></div>
				<div class="head_3"></div>
			</div>
			<div class="confirm_list">
				<Signature :checked="checked[0] ? checked[0].checked : false" :img="checked[0] ? checked[0].img : ''" @checked="showSignature(0)"
				 @cancel="signatureCancel(0)">
					<span slot>,,</span>
				</Signature>
				<Signature :checked="checked[1] ? checked[1].checked : false" :img="checked[1] ? checked[1].img : ''" @checked="showSignature(1)"
				 @cancel="signatureCancel(1)">
					<span slot>,</span>
				</Signature>
				<Signature :checked="checked[2] ? checked[2].checked : false" :img="checked[2] ? checked[2].img : ''" @checked="showSignature(2)"
				 @cancel="signatureCancel(2)">
					<div slot>,;</div>
				</Signature>
				<Signature :checked="checked[3] ? checked[3].checked : false" :img="checked[3] ? checked[3].img : ''" @checked="showSignature(3)"
				 @cancel="signatureCancel(3)">
					<div slot>,30m </div>
				</Signature>
				<Signature :checked="checked[4] ? checked[4].checked : false" :img="checked[4] ? checked[4].img : ''" @checked="showSignature(4)"
				 @cancel="signatureCancel(4)">
					<div slot>,</div>
				</Signature>
				<Signature :checked="checked[5] ? checked[5].checked : false" :img="checked[5] ? checked[5].img : ''" @checked="showSignature(5)"
				 @cancel="signatureCancel(5)">
					<div slot>,</div>
				</Signature>
				<Signature :checked="checked[6] ? checked[6].checked : false" :img="checked[6] ? checked[6].img : ''" @checked="showSignature(6)"
				 @cancel="signatureCancel(6)">
					<div slot></div>
				</Signature>
			</div>
			<!-- Popup -->
			<van-popup class="popup" v-model="signatureShow" :close-on-click-overlay="false" position="bottom">
				<Canvas ref="signature" @save="saveCanvas" @cancel="cancelCanvas"></Canvas>
			</van-popup>
			
			<div class="cell_group">
				<div class="cell border_none">
					<div class="cell_other">
						<div class="upload">
							<div class="upload_icon">
								<van-icon name="photo-o" />
							</div>
							<div class="upload_box">
								<van-uploader :before-read="beforeRead" :before-delete="beforeDelete" v-model="fileList" preview-size="5rem" />
							</div>
						</div>
					</div>
				</div>
			</div>

			<!--  -->
			<cell-textarea title="" required placeholder=""></cell-textarea>

			<!--  -->
			<cell-input title="" required placeholder=""></cell-input>

		</div>

		<!-- Popup -->
		<van-popup v-model="isShowAction" position="bottom" class="action">
			<button @click="postData"></button>
			<button></button>
			<button @click="closeAction"></button>
		</van-popup>
	</div>
</template>
<script>
	import {
		business
	} from "../../../../mixin/business";
	import {
		mapState
	} from "vuex";
	import {
		uploadFile
	} from "@/mixin/uploadFile";
	import Canvas from "@/components/Canvas.vue";
	import Signature from "../components/Signature.vue";
	export default {
		name: "mangban",
		mixins: [business, uploadFile],
		data() {
			return {
				infoId: 0,
				storeModule: "mangban",
				materialShowShow: false,
				material: {
					index: 0,
					value: ""
				},
				materialColumns: ["", "", "", ""],
				timeShow: false,
				currentDate: new Date(),
				list_1: [
					"",
					"",
					"",
					"",
					"",
					"",
					""
				],
				checked: [],
				isShowAction: false,
				sendData: {
					applyDept: this.$userInfo.officeName, // 
					applyer: this.$userInfo.userName, // 
					scMan: "", // 
					zyMan: "", // 
					otherSpecial: [], //
					checkedArr: [], // 
					signatureImage: "", //  - base64
					pipe: [{
						pipeName: "", // 
						pipeMedium: "", // 
						pipeTemp: "", // 
						pipePressure: "", // 
						pipeMaterial: "", // 
						pipeSpec: "", // 
						pipeNumber: "", // 
						pipePullTime: "", // 
						pipeBlockTime: "", // 
						pipeBlockOperator: "", // 
						pipePullOperator: "", // 
						pipeBlockGuardian: "", // 
						pipePullGuardian: "" // 
					}],
					__sid: localStorage.getItem("JiaHuaSessionId")
				},
				checked: [{
					checked: false,
					image: ""
				}],
				signatureShow: false,
				fileList: []

			};
		},
		computed: mapState({
			scMan: state => state.mangban.scMan,
			zyMan: state => state.mangban.zyMan,

			otherSpecial: state => state.mangban.otherSpecial,

			pipeBlockOperator1: state => state.mangban.pipeBlockOperator0,
			pipePullOperator1: state => state.mangban.pipePullOperator0,
			pipeBlockGuardian1: state => state.mangban.pipeBlockGuardian0,
			pipePullGuardian1: state => state.mangban.pipePullGuardian0,

			pipeBlockOperator2: state => state.mangban.pipeBlockOperator1,
			pipePullOperator2: state => state.mangban.pipePullOperator1,
			pipeBlockGuardian2: state => state.mangban.pipeBlockGuardian1,
			pipePullGuardian2: state => state.mangban.pipePullGuardian1,

			pipeBlockOperator3: state => state.mangban.pipeBlockOperator2,
			pipePullOperator3: state => state.mangban.pipePullOperator2,
			pipeBlockGuardian3: state => state.mangban.pipeBlockGuardian2,
			pipePullGuardian3: state => state.mangban.pipePullGuardian2,

			pipeBlockOperator4: state => state.mangban.pipeBlockOperator3,
			pipePullOperator4: state => state.mangban.pipePullOperator3,
			pipeBlockGuardian4: state => state.mangban.pipeBlockGuardian3,
			pipePullGuardian4: state => state.mangban.pipePullGuardian3
		}),
		components: {
			Canvas,
			Signature
		},
		mixins: [business],
		created() {

			this.infoId = this.$route.query.id || 0;
			this.getInfo();

		},
		methods: {

			getInfo() {

				this.$api.page_3
					.htHseMbzypListData({
						__sid: localStorage.getItem("JiaHuaSessionId"),
						id: this.infoId
					})
					.then(res => {
						this.listData = res.list;
						this.isLoading = false
						console.log(this.listData)
					}).catch(() => {});

			},

			beforeRead() {},
			beforeDelete() {

			},

			saveCanvas(e) {
				this.signatureShow = false;
				this.checked[this.selectSignatureShow] = {
					checked: false,
					img: ""
				};
				this.checked[this.selectSignatureShow].img = e;
				this.sendData.signatureImage = e;
				console.log("signatureShow: ");
			},
			cancelCanvas() {
				this.checked[this.selectSignatureShow].checked = false;
				this.checked[this.selectSignatureShow].img = "";
				this.signatureShow = false;
			},
			// 
			showSignature(index) {
				console.log("index: ", index);
				console.log("");
				this.selectSignatureShow = index;
				this.signatureShow = true;
				this.checkedArr.push(index);
			},
			// 
			signatureCancel(index) {
				console.log("index: ", index);
				console.log("");
				this.checked[index].checked = false;
				this.checked[index].img = "";
			},

			// Popup
			openAction() {
				this.isShowAction = true;
			},

			// Popup
			closeAction() {
				this.isShowAction = false;
			},

			pageBack() {
				this.$router.back();
			},
			// 
			onMaterialConfirm(value, index) {
				this.materialShowShow = false;
				this.material = {
					index: index,
					value: value
				};
			},

			postData() {
				// console.log(this.sendData);
				let sendData = JSON.parse(JSON.stringify(this.sendData));
				let pipe = sendData.pipe;
				let dataStrArr = [];

				function formatMaterial(str) {
					if (str == ``) {
						return 1;
					}
					if (str == ``) {
						return 2;
					}
					if (str == ``) {
						return 3;
					}
					if (str == ``) {
						return 4;
					}
				}

				for (let i = 0; i <= 3; i++) {
					console.log(i);
					if (pipe[i] == undefined) {
						dataStrArr[i] = `|||||||||||&|&`;
						continue;
					}
					console.log(888);
					// console.log(pipe[i].pipeBlockGuardian[0].userName)
					console.log(pipe[i].pipeMaterial);
					dataStrArr[i] =
						`${pipe[i].pipeName}|${pipe[i].pipeMedium}|${
          pipe[i].pipePressure
        }|${formatMaterial(pipe[i].pipeMaterial)}|${pipe[i].pipeSpec}|${
          pipe[i].pipeNumber
        }|${pipe[i].pipeBlockTime}|${pipe[i].pipePullTime}|${
          pipe[i].pipeBlockOperator[0].userName
        }|${pipe[i].pipePullOperator[0].userName}|${
          pipe[i].pipeBlockGuardian[0].userName
        }|${pipe[i].pipePullGuardian[0].userName}`;
				}
				let finSendData = {
					applyDept: sendData.applyDept,
					applyer: sendData.applyer,
					scMan: sendData.scMan,
					zyMan: sendData.zyMan,
					otherSpecial: sendData.otherSpecial,
					onePipe: dataStrArr[0],
					twoPipe: dataStrArr[1],
					threePipe: dataStrArr[2],
					fourPipe: dataStrArr[3],
					num: String(sendData.checkedArr),
					affirmRen: sendData.signatureImage,
					__sid: sendData.__sid
				};
				console.log(finSendData);

				this.$api.page_3.htHseMbzypSave(finSendData).then(res => {
					console.log(res);
				});
			},

			// 
			onMaterialCancel() {
				this.materialShowShow = false;
			},
			// 
			onTimeCancel() {
				this.timeShow = false;
			},
			// 
			onTimeConfirm() {
				this.timeShow = false;
			},
			toSelect(index) {
				this.$router.push({
					path: "./select",
					query: {
						index: index,
						showList: this[`list_${index}`]
					}
				});
			},
			// 
			addDevice() {
				if (this.sendData.pipe.length < 4) {
					this.sendData.pipe.push({
						pipeName: "", // 
						pipeMedium: "", // 
						pipeTemp: "", // 
						pipePressure: "", // 
						pipeMaterial: "", // 
						pipeSpec: "", // 
						pipeNumber: "", // 
						pipePullTime: "", // 
						pipeBlockTime: "", // 
						pipeBlockOperator: "", // 
						pipePullOperator: "", // 
						pipeBlockGuardian: "", // 
						pipePullGuardian: "" // 
					});
				}
			}
		},
		watch: {
			zyMan(res) {
				this.sendData.zyMan = res;
			},
			scMan(res) {
				this.sendData.scMan = res;
			},

			otherSpecial(res) {
				this.sendData.otherSpecial = res;
			},

			pipeBlockOperator1(res) {
				this.sendData.pipe[0].pipeBlockOperator = res;
			},
			pipePullOperator1(res) {
				this.sendData.pipe[0].pipePullOperator = res;
			},
			pipeBlockGuardian1(res) {
				this.sendData.pipe[0].pipeBlockGuardian = res;
			},
			pipePullGuardian1(res) {
				this.sendData.pipe[0].pipePullGuardian = res;
			},

			pipeBlockOperator2(res) {
				this.sendData.pipe[1].pipeBlockOperator = res;
			},
			pipePullOperator2(res) {
				this.sendData.pipe[1].pipePullOperator = res;
			},
			pipeBlockGuardian2(res) {
				this.sendData.pipe[1].pipeBlockGuardian = res;
			},
			pipePullGuardian2(res) {
				this.sendData.pipe[1].pipePullGuardian = res;
			},

			pipeBlockOperator3(res) {
				this.sendData.pipe[2].pipeBlockOperator = res;
			},
			pipePullOperator3(res) {
				this.sendData.pipe[2].pipePullOperator = res;
			},
			pipeBlockGuardian3(res) {
				this.sendData.pipe[2].pipeBlockGuardian = res;
			},
			pipePullGuardian3(res) {
				this.sendData.pipe[2].pipePullGuardian = res;
			},

			pipeBlockOperator4(res) {
				this.sendData.pipe[3].pipeBlockOperator = res;
			},
			pipePullOperator4(res) {
				this.sendData.pipe[3].pipePullOperator = res;
			},
			pipeBlockGuardian4(res) {
				this.sendData.pipe[3].pipeBlockGuardian = res;
			},
			pipePullGuardian4(res) {
				this.sendData.pipe[3].pipePullGuardian = res;
			}
		}
	};
</script>
<style lang="scss" scoped>
	@import "@/assets/scss/cell.scss";

	.mangban {
		background-color: #f5f5f5;

		.next {
			width: 100%;
			height: 98px;
			margin-top: 25px;
			font-size: 32px;
			text-align: center;
			color: rgba(255, 255, 255, 1);
			line-height: 98px;
			background: rgba(96, 150, 248, 1);
			box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.5);
		}
	}

	.guandao-id {
		background-color: rgb(0, 142, 225) !important;
	}

	.action {
		padding-left: 30px;
		padding-right: 30px;
		background-color: transparent;

		button {
			width: 100%;
			height: 110px;
			margin-bottom: 20px;
			background-color: white;
			border: none;
			border-radius: 30px;
			color: rgb(0, 118, 255);
			font-size: 35px;
		}
	}

	.cell {
		background-color: white;
	}

	.header-cell {
		margin-bottom: 30px;
	}
</style>
